import React, { Component } from 'react';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
export default class ContactUs extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <Container>
        <Row>
          <Col sm={12}>
            <h2 class="text-center">Projects</h2>
            <hr/>
            <div class="projects-group">
            {
            resumeData.Projects && resumeData.Projects.map((item)=>{
              return(
                <div>
                  <div class="col-exp">
                    <div class="col-exp-emp">
                      <h4>{item.ProjectName} - {item.ClientName}</h4>
                      <div><b>Role:</b> {item.Position}</div>
                      <div><b>Time Period:</b> {item.PeriodOfTime}</div>
                      <div class="project-desc">{item.Description}</div>
                    </div>
                  </div>
                  <hr/>
                </div>
                )
              })
            }
            </div>
          </Col>
        </Row>
      </Container>
        );
  }
}